// Hacker News ÌîºÎìú Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
const ajax = new XMLHttpRequest();
const NEWS_URL = "https://api.hnpwa.com/v0/news/1.json";
const container = document.querySelector(".container");
const postsPerPage = 8; // 1ÌéòÏù¥ÏßÄ Îãπ Í≤åÏãúÎ¨º Ïàò
let store = {};
let currentPage = 1;

function getData(url) {
  ajax.open("GET", url, false);
  ajax.send();
  return JSON.parse(ajax.response);
}

// newsFeed Î∂àÎü¨Ïò§Í∏∞
function getNewsFeed() {
  const newsFeed = getData(NEWS_URL);
  const lastPage = parseInt(newsFeed.length / postsPerPage);

  const source = `
    <ul>
      {{#each list}}
      <li>
        <div><a href="{{url}}">{{title}} ({{domain}})</a></div>
        <div>
          <span>{{points}} points by {{user}} {{time_ago}}</span>
          <span><a href="{{individual_url}}">{{comments_count}} comments</a></span>
        </div>
      </li>
      {{/each}}
    </ul> 
    <div class="page">
        <span><a href="#news?p={{prev_page}}">Prev</a></span>
        <span><a href="#news?p={{next_page}}">Next</a></span>
    </div>
    `;

  store = {
    list: newsFeed.slice((currentPage - 1) * postsPerPage, currentPage * postsPerPage),
    // Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ, Îã§Ïùå ÌéòÏù¥ÏßÄ Íµ¨ÌòÑ(ÏÇºÌï≠ Ï°∞Í±¥ Ïó∞ÏÇ∞Ïûê ÏÇ¨Ïö©)
    prev_page: currentPage > 1 ? currentPage - 1 : currentPage,
    next_page: currentPage < lastPage ? currentPage + 1 : lastPage,
  };

  // ‚úÖpostsPerPageÏù¥ ÎÇòÎàÑÏñ¥Îñ®Ïñ¥ÏßÄÎäî Ïà´ÏûêÍ∞Ä ÏïÑÎãêÎïå, ÎßàÏßÄÎßâ ÌéòÏù¥ÏßÄ Ï∂úÎ†•ÎêòÏßÄ ÏïäÎäî error Ìï¥Í≤∞
  // ‚úÖi < newsFeed.lengthÎ°ú ÏûòÎ™ª Íµ¨ÌòÑÌï®.
  // newsFeedÎ•º slice Ìï¥ÏÑú Ïù¥ÎØ∏ ÏÉàÎ°úÏö¥(indexÎèÑ ÏÉàÎ°úÏõåÏßê) newsFeed Î∞∞Ïó¥ÏùÑ ÎßåÎì§ÏóàÏúºÎØÄÎ°ú,
  // Ïù¥Ï†úÎäî newsFeed ÎåÄÏã† store.listÎ•º ÏÇ¨Ïö©Ìï¥Ïïº ÎêúÎã§.
  for (let i = 0; i < store.list.length; i++) {
    store.list[i].individual_url = `#item?id=${store.list[i].id}`;
  }

  let template = Handlebars.compile(source);
  container.innerHTML = template(store);
}

// ÌÅ¥Î¶≠Ìïú Í∏ÄÏùò idÎ•º Ï†ÑÎã¨Ìï¥ÏÑú ÏΩòÌÖêÏ∏† ÌôîÎ©¥ Î∂àÎü¨Ïò§Í∏∞
function getIndividualContents(id) {
  const CONTENT_URL = `https://api.hnpwa.com/v0/item/${id}/json`;
  const contents = getData(CONTENT_URL);
  const source = `
  <div class="title">
    <h1>
      <a href="{{url}}">{{title}} ({{domain}})</a>
    </h1>
    <div>
      <span>{{points}} points</span>
      <span>by {{user}}</span>
      <span>{{time_ago}}</span>
      <span>{{comments_count}} comments</span>
    </div>
  </div>

  <ul>
  </ul>
  `;

  store = {
    title: contents.title,
    url: contents.url,
    domain: contents.domain,
    points: contents.points,
    user: contents.user,
    time_ago: contents.time_ago,
    comments_count: contents.comments_count,
  };

  const template = Handlebars.compile(source);
  container.innerHTML = template(store);

  // commentsÏùò htmlÏùÑ ulÏùò innerHTMLÏúºÎ°ú ÎÑ£Í∏∞
  function makeComments(comments, called = 0) {
    const commentString = [];
    for (let i = 0; i < comments.length; i++) {
      commentString.push(`
        <li>
          <div style = "padding-left: ${called * 2.5}rem"}>${comments[i].user} ${comments[i].time_ago}</div>
          <div style = "padding-left: ${called * 2.5}rem">${comments[i].content}</div>
        </li> 
    `);
      if (comments[i].comments_count > 0) {
        commentString.push(makeComments(comments[i].comments, called + 1));
      }
    }
    return commentString.join("");
  }

  document.querySelector(".container ul").innerHTML = makeComments(contents.comments);
}

// ÎùºÏö∞ÌÑ∞ Íµ¨ÌòÑ
function router() {
  const hash = location.hash;
  if (hash === "") {
    getNewsFeed();
  } else if (hash.substr(1, 7) === "news?p=") {
    // currentPage Í∞±Ïã†
    currentPage = Number(hash.substr(8));
    getNewsFeed();
  } else {
    getIndividualContents(hash.substr(9, location.hash.length));
  }
}
window.addEventListener("hashchange", router);
router();
